<template>
  <div>
    <article-view :item="item"/>
    <a href="">Home</a>
  </div>
</template>

<script>
import articleView from '@/components/ArticleView';
import { getBlog } from '@/data/load';

export default {
  components: {
    articleView,
  },
  props: {
    blogId: {
      default: null,
      type: String,
    },
  },
  data() {
    return {
      item: undefined,
    };
  },
  methods: {
    getBlog,
    loadItem() {
      this.getBlog(this.blogId).then((x) => { this.item = x; });
    },
  },
  mounted() {
    this.loadItem();
  },
  watch: {
    $route() {
      this.loadItem();
    },
  },
};
</script>



