<template>
  <div v-if="item !== null">
    <h3> {{ item.title }} </h3>
    <div v-html="item.content"/>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      default: null,
      type: Object,
    },
  },
};
</script>



